<template>
  <div class="my">
    <b-header title="My">
      <div @click="logout" class="btn-logout">Sign Out</div>
    </b-header>
    <section class="main">
      <div class="container">
        <div class="avatar">
          <img :src="user.avatar" alt="avatar" />
          <p>{{ user.name }}</p>
        </div>
        <div class="info">
          <p>
            <span>Username</span>
            <span>: </span>
            <span>{{ user.name }}</span>
          </p>
          <p>
            <span>Email</span>
            <span>: </span>
            <span>{{ user.email }}</span>
          </p>
        </div>
      </div>
      <router-link tag="p" class="change" to="/change-info">
        Go to change
      </router-link>
    </section>
    <!-- <router-view></router-view> -->
  </div>
</template>
<script>
import BHeader from 'components/header'
import { USERINFO_KEY } from '@/config'
import storage from 'assets/js/storage'
export default {
  name: 'my',
  beforeRouteEnter(to, from, next) {
    const auth = {
      isLogin() {
        return true
      },
    }
    console.log('navigation guards...')
    if (auth.isLogin()) {
      next()
    } else {
      next({ name: 'user' })
    }
  },
  data() {
    return {
      user: {
        name: 'molly',
        email: 'molly@hotmail.com',
        avatar: require('assets/userAvatar/15820256995611.jpg'),
      },
    }
  },
  methods: {
    // logout and delete the user info in localstorage
    logout() {
      storage.remove(USERINFO_KEY)
      this.$router.push({ path: '/' })
    },
  },
  components: {
    BHeader,
  },
}
</script>
<style lang="scss" scoped>
@import '~assets/scss/mixins';
@media screen and (min-width: 720px) {
  .my {
    position: fixed;
    width: 100%;
    top: 0;
    bottom: 0;
    background-color: $background-color;
    .header {
      width: 80%;
      height: 60px;
      position: fixed;
      left: 0;
      top: 0;
      .btn-logout {
        padding: 0 10px;
        background-color: #00a1d6;
        color: white;
        border-radius: 5px;
        font-size: 15px;
        &:hover {
          cursor: pointer;
        }
      }
    }
    .main {
      width: 80%;
      height: calc(100% - 90px);
      margin: 75px auto 0;
      box-shadow: 1px 0 5px rgba($color: #000000, $alpha: 0.2);
      box-sizing: border-box;
      padding: 20px;
      background-color: white;
      .container {
        width: 80%;
        height: 350px;
        margin: 0 auto;
        background-color: white;
        border-radius: 20px;
        position: relative;
        margin-top: 50px;
        box-shadow: 1px 0px 5px rgba(0, 0, 0, 0.2);
        .avatar {
          @include flex-center(column);
          position: absolute;
          top: -40px;
          left: 50%;
          transform: translateX(-50%);
          font-size: 20px;
          color: #575757;
          font-weight: bold;
          img {
            width: 80px;
            height: 100%;
            border-radius: 50%;
            margin-bottom: 8px;
            box-shadow: 1px 0px 5px rgba(0, 0, 0, 0.2);
          }
        }
        .info {
          width: 80%;
          margin: 0 auto;
          padding: 80px 0;
          text-align: center;
          p {
            width: 60%;
            min-width: 410px;
            margin: 10px auto 25px;
            padding: 10px 0;
            color: #575757;
            border: 1px solid #575757;
            display: flex;
            justify-content: center;
            user-select: none;
            span {
              margin-right: 5px;
              &:first-child {
                display: inline-block;
                width: 100px;
                text-align: right;
              }
              &:last-child {
                display: inline-block;
                width: 300px;
                text-align: left;
                margin-right: 0;
              }
            }
          }
        }
      }
      .change {
        width: 130px;
        height: 32px;
        line-height: 32px;
        text-align: center;
        border: 1px solid #575757;
        border-radius: 3px;
        color: #575757;
        margin: 30px auto 0;
        &:hover {
          background-color: #7e7c7c;
          border: 1px solid #7e7c7c;
          color: white;
          cursor: pointer;
        }
      }
    }
  }
}
@media screen and (max-width: 719px) {
}
</style>
